import React,{useState} from 'react';
import db from '../db.js'
import t from '../components/t.js'
import { Table } from 'antd';

 // { name: '',
  //   date: '2 5',
  //   dateNum: 34,
  //   class: '',
  //   grade: 'G3',
  //   place: '',
  //   ground: '',

  //   distance: '1400',
  //   distanceType: '',
  //   order: '' }
  // { class: [ '', '', '', '' ],
  // grade: [ 'OP', 'G3', 'Pre-OP', 'G2', 'G1' ],
  // place: [ '', '', '', '', '', '', '', '', '', '', '' ],
  // ground: [ '', '' ],
  // distanceType: [ '', '', '', '' ],
  // direction: [ '', '', '' ],
  // side: [ null, '', '', '' ] }
 const filterList= { class:
    [ { text: '/', value: '' },
      { text: '/', value: '' },
      { text: '&/', value: '' },
      { text: '/', value: '' } ],
   grade:
   [
      { text: 'Pre-OP', value: 'Pre-OP' },
      { text: 'OP', value: 'OP' },
      { text: 'G1', value: 'G1' },
      { text: 'G2', value: 'G2' },
      { text: 'G3', value: 'G3' }
    ],
   place:
    [ { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' } ],
   ground: [ { text: '', value: '' }, { text: '', value: '' } ],
   distanceType:
   [
     { text: '', value: '' },
     { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' } ],
   direction:
    [ { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' } ],
   side:
    [ { text: '', value: null },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' } ] }
  const labels = ["name", "date", "class", "grade",
                  "place", "ground", "distance",
                  "distanceType", "direction", "side"]
  const labelTextDict ={name:t(""),date:t(""),class:t(""),grade:t(""),place:t(""),ground:t(""),distance:t(""),distanceType:t(""),direction:t(""),side:t("")}
  const getCorrespondingLabelText = (label)=>{
      return labelTextDict[label]
  }
  const mediumLabels = ["name", "date", "class","grade","ground","distanceType"]
  const getColumns = (labels)=>{
    return labels.map(label=> {
      if(filterList['class']){
        return {
          title:getCorrespondingLabelText(label),
          dataIndex:label,
          filters:filterList[label],
          onFilter: (value, record) => record[label] === value,
        }
      }else{
        return{
          title:getCorrespondingLabelText(label),
          dataIndex:label
        }
      }
    })
  }



  const Race = (props) =>{
    const useViewport = () => {
      // const [width, setWidth] = React.useState(window.innerWidth);
      const [height,setHeight] = React.useState(window.innerHeight);
      React.useEffect(() => {
        const handleWindowResize = () => setHeight(window.innerHeight);
        window.addEventListener("resize", handleWindowResize);
        return () => window.removeEventListener("resize", handleWindowResize);
      }, []);
      console.log('currentWidth::',height);
      return {height};
    };

    const dynamicTableHeight = useViewport().height -300;

    const allRaceList = db.get('races').value().map((race,index)=>{
                      race.key=index
                      return race
                    })
    const [selectedRowKeys,setSelectedRowKeys] = useState([])
    let columns = getColumns(labels)
    if(props.type==='medium'){
      columns = getColumns(mediumLabels)
    }
    const onSelectChange = (selectedRowKeys,selectedRows)=>{
      let selected = {}
      for(let race of selectedRows){
        if(selected[race.dateNum]){
          selected[race.dateNum].push(race.id)
        }else{
          selected[race.dateNum]=[race.id]
        }
      }
      console.log(selected)
      props.onSelect(selected)
      setSelectedRowKeys(selectedRowKeys)
    }
    const rowSelection = {
      selectedRowKeys,
      onChange:onSelectChange
    }
    // 
    const onChange = (pagination, filters, sorter, extra) => {
      props.onSelect([])
      setSelectedRowKeys([])
    }
    return(
      <div>
      <Table rowSelection={props.onSelect?rowSelection:null} columns={columns}
      dataSource={allRaceList} onChange={onChange} pagination={false} scroll={{y:dynamicTableHeight}}/>
      </div>
      )
  }

export default Race
