import React,{useState} from 'react';

import { Picker, List,Flex,Card } from 'antd-mobile';

import { Image,Button,Divider,Modal,Rate,Form} from 'antd';
import {message} from 'antd'
//test
import {PlusOutlined,SmileOutlined,FrownOutlined,CopyOutlined } from '@ant-design/icons';
import {CopyToClipboard} from 'react-copy-to-clipboard';

import axios from 'axios'
import db from '../db.js'
import t from '../components/t.js'
import Player from '../pages/player.js'
import Support from '../pages/support.js'
const cdnServer = 'https://cdn.jsdelivr.net/gh/wrrwrr111/pretty-derby/public/'
let userId = db.get('userId').value()

const blueLabels={
  'speed':'',
  'stamina':'',
  'power':'',
  'guts':'',
  'wisdom':''
}
const redLabels = {
  'grass':'',
  'dirt':'',
  'shortDistance':'',
  'mile':'',
  'mediumDistance':'',
  'longDistance':'',
  'escapeR':'',
  'leadingR':'',
  'insertR':'',
  'trackingR':''}

const lists = [
  [
    {value:'speed',label:t('')},
    {value:'stamina',label:t('')},
    {value:'power',label:t('')},
    {value:'guts',label:t('')},
    {value:'wisdom',label:t('')},
    {value:'grass',label:t('')},
    {value:'dirt',label:t('')},
    {value:'shortDistance',label:t('')},
    {value:'mile',label:t('')},
    {value:'mediumDistance',label:t('')},
    {value:'longDistance',label:t('')},
    {value:'escapeR',label:t('')},
    {value:'leadingR',label:t('')},
    {value:'insertR',label:t('')},
    {value:'trackingR',label:t('')}
  ],[
    {value:1,label:`1${t('')}`},
    {value:2,label:`2${t('')}`},
    {value:3,label:`3${t('')}`},
    {value:4,label:`4${t('')}`},
    {value:5,label:`5${t('')}`},
    {value:6,label:`6${t('')}`},
    {value:7,label:`7${t('')}`},
    {value:8,label:`8${t('')}`},
    {value:9,label:`9${t('')}`},
  ]
]



const PlayerInput = ({ value = {}, onChange }) => {
  const [isPlayerVisible, setIsPlayerVisible] = useState(false);
  const [data, setData] = useState({});
  const showPlayer = (index) => {
    setIsPlayerVisible(true);
  };
  const closePlayer = () => {
    setIsPlayerVisible(false);
  };
  const triggerChange = (changedValue)=>{
    setIsPlayerVisible(false);
    onChange?.({
      ...data,
      ...value,
      ...changedValue,
    })
  }
  const handleSelectPlayer = (data)=>{
    setData(data)
    triggerChange(data)
  }
  return (
    <>
      <Image src={data.imgUrl?cdnServer+data.imgUrl:''}
        preview={false} fallback="data:image/png;base64,"
        width={'100%'} style={{minWidth:'100px'}} onClick={showPlayer}>
      </Image>
        <Modal visible={isPlayerVisible} onOk={closePlayer} onCancel={closePlayer} width={'80%'}>
          <Player onSelect={handleSelectPlayer}></Player>
        </Modal>
    </>
  );
};
const SupportInput = ({ value = {}, onChange }) => {
  const [isSupportVisible, setIsSupportVisible] = useState(false);
  const [data, setData] = useState({});
  const showSupport = (index) => {
    setIsSupportVisible(true);
  };
  const closeSupport = () => {
    setIsSupportVisible(false);
  };
  const triggerChange = (changedValue)=>{
    setIsSupportVisible(false);
    onChange?.({
      ...data,
      ...value,
      ...changedValue,
    })
  }
  const handleSelectSupport = (data)=>{
    setData(data)
    triggerChange(data)
  }
  return (
    <>
      <Image src={data.imgUrl?cdnServer+data.imgUrl:''}
        preview={false} fallback="data:image/png;base64,"
        width={'100%'} style={{minWidth:'100px'}}  onClick={showSupport}>
      </Image>
        <Modal visible={isSupportVisible} onOk={closeSupport} onCancel={closeSupport} width={'80%'}>
          <Support onSelect={handleSelectSupport}></Support>
        </Modal>
    </>
  );
};

const SearchOne = (props)=>{
  return(
    <Form.List name={props.name}>
      {(fields, { add, remove }, { errors }) => (
          <List>
            {fields.map((field, index) => (
                <Form.Item {...field} key={field.key}
                  name={[field.name,'attr']}
                  fieldKey={[field.fieldKey,'attr']}
                  rules={[{required: true}]} validateTrigger={['onChange', 'onBlur']} noStyle>
                      <Picker
                        data={lists}
                        cascade={false}
                        extra={t('')}
                        ><List.Item arrow="horizontal">{t('()')}</List.Item>
                      </Picker>
                </Form.Item>
            ))}
            <List.Item>
                <Button
                  type="dashed"
                  onClick={() => add()}
                  icon={<PlusOutlined />}
                  >
                  {t('')}
                </Button>
            </List.Item>
          </List>
        )}
      </Form.List>
  )
}
const SearchForm = (props)=>{
  const [form] = Form.useForm()

  const onFinish = async (value)=>{
    let formData = {attrs:[],levels:[]}
    if(value['player0']){
      formData.attrs.push('playerId0')
      formData.levels.push(value['player0'].id)
    }
    if(value['support']){
      formData.attrs.push('supportId')
      formData.levels.push(value['support'].id)
    }
    value.p1?.forEach(item=>{
      formData.attrs.push(item.attr[0])
      formData.levels.push(item.attr[1])
    })

    props.search(formData)
  }
  const onReset = ()=>{
    form.resetFields();
  }
  return(
    <Form name='' form={form} onFinish={onFinish} className="seed-form">
      <SearchOne name='p1'></SearchOne>
      <Flex align='start'>
        <Flex.Item>
          <p>{t('')}</p>
          <Form.Item name={`player0`}>
              <PlayerInput></PlayerInput>
          </Form.Item>
        </Flex.Item>
        <Flex.Item>
          <p>{t('')}</p>
          <Form.Item name={'support'}>
            <SupportInput></SupportInput>
          </Form.Item>
        </Flex.Item>
        <Flex.Item>
          <p>{t('')}</p>
          <Form.Item name={'supportLevel'} initialValue={0}>
            <Rate count={4} />
          </Form.Item>
        </Flex.Item>
      </Flex>
      <Flex justify='end'>
        <Form.Item>
          <Button htmlType="button" onClick={()=>onReset()}>
            {t('')}
          </Button>
        </Form.Item>
        <Form.Item>
          <Button type="primary" htmlType="submit">
            {t('')}
          </Button>
        </Form.Item>
      </Flex>
    </Form>
  )
}
const PlayerImage = (props)=>{
  let imgUrl =  db.get('players').find({id:props.id}).value().imgUrl
  return <Image src={cdnServer+imgUrl} fallback="data:image/png;base64,"
        width={80}  preview={false}></Image>
}
const SupportImage = (props)=>{
  let support = db.get('supports').find({id:props.id}).value()
  let imgUrl = ''
  if(support){
    imgUrl =  support.imgUrl
  }
  return <Image src={cdnServer+imgUrl} fallback="data:image/png;base64,"
        width={80} preview={false}></Image>
}

const Seed = ()=>{
  const SeedCard = (props) =>{
    const data = props.data
    return (
      <Card>
        <Flex>
            <p style={{fontSize:'1.5em'}}>{data.gameId}</p>
            <CopyToClipboard text={data.gameId} onCopy={()=>message.info('')}>
              <Button shape="circle" icon={<CopyOutlined />}/>
            </CopyToClipboard>
        </Flex>
        <Flex>
          <Flex.Item>
            <PlayerImage id={data['playerId0']}></PlayerImage>
            <SupportImage id={data['supportId']}></SupportImage>
            <Flex align='middle'>
              <Button shape="circle" icon={<SmileOutlined />} onClick={()=>like(data)}/>
              <p>{data.likes}</p>
            </Flex>
            <Flex align='middle'>
              <Button shape="circle" icon={<FrownOutlined />} onClick={()=>dislike(data)}/>
              <p>{data.dislikes}</p>
            </Flex>
          </Flex.Item>
          <Flex.Item>
            <p>{`${t(blueLabels[data['blue0']])}: ${data['blueLevel0']}`}</p>
            <p>{`${t(redLabels[data['red0']])}: ${data['redLevel0']}`}</p>
            {Object.keys(blueLabels).map(key=>
            data[key]?<p key={key}>{`${t('')} ${t(blueLabels[key])}: ${data[key]}`}</p>:null
            )}
            {Object.keys(redLabels).map(key=>
            data[key]?<p key={key}>{`${t('')} ${t(redLabels[key])}: ${data[key]}`}</p>:null
            )}
            <p>{`${t('')}: ${data['supportLevel']||0}`}</p>
          </Flex.Item>
        </Flex>
      </Card>
    )
  }

  const [seedList,setSeedList] = useState([])
  const search = async (value)=>{
    const res = await axios.post('https://urarawin.com/api/sqlite/search',value)
    if(res.data){
      if(res.data.count){
        setSeedList([...res.data.list])
      }else{
        message.info(t(''))
      }
    }else{
      message.info(t(''))
    }
  }
  const like = async (seed) =>{
    if(!userId){
      message.info(t(''))
      return
    }
    let id = seed.id
    const res = await axios.post('https://urarawin.com/api/sqlite/like',{id,userId})
    if(res.data){
      message.info(t(''))
      seed.likes+=1
    }
    setSeedList([...seedList])
  }
  const dislike = async (seed)=>{
    if(!userId){
      message.info(t(''))
      return
    }else if(seed.dislikes&&seed.dislikes.indexOf(userId)!==-1 ){
      return
    }
    let id = seed.id
    const res = await axios.post('https://urarawin.com/api/sqlite/dislike',{id,userId})
    if(res.data){
      message.info(t(''))
      seed.dislikes+=1
    }
    setSeedList([...seedList])
  }
  return(
  <>
    <SearchForm search={search}></SearchForm>
    <Divider>{t('')}</Divider>
    <Flex wrap='wrap'>
      {seedList.map(seed=>
      <Flex.Item  key={seed.gameId}
        style={{width:'48%',flex:'0 0 auto',margin:0}}
      >
        <SeedCard data={seed}></SeedCard>
      </Flex.Item>)}
    </Flex>
    {/* <Table columns={columns} dataSource={seedList} pagination={false} rowKey={'id'}/> */}
  </>
  )
}
export default Seed
