// userId

const blueLabels = {
  speed: "",
  stamina: "",
  power: "",
  guts: "",
  wisdom: "",
};
const redLabels = {
  grass: "",
  dirt: "",
  shortDistance: "",
  mile: "",
  mediumDistance: "",
  longDistance: "",
  escapeR: "",
  leadingR: "",
  insertR: "",
  trackingR: "",
};

const PlayerInput = ({ value = {}, onChange }) => {
  const [isPlayerVisible, setIsPlayerVisible] = useState(false);
  const [data, setData] = useState({});
  const showPlayer = (index) => {
    setIsPlayerVisible(true);
  };
  const closePlayer = () => {
    setIsPlayerVisible(false);
  };
  const triggerChange = (changedValue) => {
    setIsPlayerVisible(false);
    onChange?.({
      ...data,
      ...value,
      ...changedValue,
    });
  };
  const handleSelectPlayer = (data) => {
    setData(data);
    triggerChange(data);
  };
  return (
    <>
      <Image
        src={data.imgUrl ? cdnServer + data.imgUrl : ""}
        preview={false}
        fallback="data:image/png;base64,"
        width={80}
        onClick={showPlayer}
      ></Image>
      <Modal visible={isPlayerVisible} onOk={closePlayer} onCancel={closePlayer} width={"80%"}>
        <Player onSelect={handleSelectPlayer}></Player>
      </Modal>
    </>
  );
};
const SupportInput = ({ value = {}, onChange }) => {
  const [isSupportVisible, setIsSupportVisible] = useState(false);
  const [data, setData] = useState({});
  const showSupport = (index) => {
    setIsSupportVisible(true);
  };
  const closeSupport = () => {
    setIsSupportVisible(false);
  };
  const triggerChange = (changedValue) => {
    setIsSupportVisible(false);
    onChange?.({
      ...data,
      ...value,
      ...changedValue,
    });
  };
  const handleSelectSupport = (data) => {
    setData(data);
    triggerChange(data);
  };
  return (
    <>
      <Image
        src={data.imgUrl ? cdnServer + data.imgUrl : ""}
        preview={false}
        fallback="data:image/png;base64,"
        width={80}
        onClick={showSupport}
      ></Image>
      <Modal visible={isSupportVisible} onOk={closeSupport} onCancel={closeSupport} width={"80%"}>
        <Support onSelect={handleSelectSupport}></Support>
      </Modal>
    </>
  );
};
const SeedInput = (props) => {
  const [form] = Form.useForm();
  const [seed, setSeed] = useState({});

  const onFinish = async (value) => {
    let tmpSeed = seed;
    tmpSeed = Object.assign({}, seed, value);
    setSeed(tmpSeed);
    // db.set('form',form).write()
    // 
    const formData = { ...tmpSeed };
    formData.userId = userId || dbL.get("userId").value();
    let updateTime = new Date().getTime();
    formData.updateTime = updateTime;
    formData["playerId0"] = value["player0"].id;
    formData["playerId1"] = value["player1"].id;
    formData["playerId2"] = value["player2"].id;
    formData["supportId"] = value["support"].id;
    delete formData["support"];
    delete formData["player0"];
    delete formData["player1"];
    delete formData["player2"];
    // formData[formData['blue0']]=formData['blueLevel0']
    // formData[formData['red0']]=formData['redLevel0']
    let list = ["0", "1", "2"];
    list.forEach((i) => {
      //
      if (formData[formData[`blue${i}`]] !== undefined) {
        formData[formData[`blue${i}`]] += formData[`blueLevel${i}`];
      } else {
        formData[formData[`blue${i}`]] = formData[`blueLevel${i}`];
      }
      //
      if (formData[formData[`red${i}`]] !== undefined) {
        formData[formData[`red${i}`]] += formData[`redLevel${i}`];
      } else {
        formData[formData[`red${i}`]] = formData[`redLevel${i}`];
      }
      //
      if (formData["white"] !== undefined) {
        formData["white"] += formData[`whiteNum${i}`];
      } else {
        formData["white"] = formData[`whiteNum${i}`];
      }
      //ura
      if (formData["uraLevel"] !== undefined) {
        formData['uraLevel'] += formData[`uraLevel${i}`]
      } else {
        formData['uraLevel'] = formData[`uraLevel${i}`]
      }

    });
    const res = await axios.post("https://urarawin.com/api/sqlite/add", formData);
    if (res.data && res.data.success) {
      message.info("");
      props.onFinish();
    } else {
      message.info("");
    }
  };
  const list = ["", "1", "2"];

  const getFields = () => {
    const children = [];
    for (let i = 0; i < 3; i++) {
      children.push(
        <Col span={8} key={i}>
          <PageHeader title={`${list[i]}`}></PageHeader>
          <Row>
            <Col span={6} offset={4}>
              <Form.Item label="" name={`player${i}`} rules={[{ required: true }]}>
                <PlayerInput></PlayerInput>
              </Form.Item>
            </Col>
          </Row>
          <Form.Item label="" name={`blue${i}`} rules={[{ required: true }]}>
            <Radio.Group>
              <Radio.Button value={"speed"}>{""}</Radio.Button>
              <Radio.Button value={"stamina"}>{""}</Radio.Button>
              <Radio.Button value={"power"}>{""}</Radio.Button>
              <Radio.Button value={"guts"}>{""}</Radio.Button>
              <Radio.Button value={"wisdom"}>{""}</Radio.Button>
            </Radio.Group>
          </Form.Item>
          <Form.Item label="" name={`blueLevel${i}`} rules={[{ required: true }]}>
            <Rate count={3} />
          </Form.Item>
          <Form.Item label="" name={`red${i}`} rules={[{ required: true }]}>
            <Radio.Group>
              <Radio.Button value={"grass"}>{"/"}</Radio.Button>
              <Radio.Button value={"dirt"}>{"/"}</Radio.Button>
              <hr />
              <Radio.Button value={"shortDistance"}>{""}</Radio.Button>
              <Radio.Button value={"mile"}>{""}</Radio.Button>
              <Radio.Button value={"mediumDistance"}>{""}</Radio.Button>
              <Radio.Button value={"longDistance"}>{""}</Radio.Button>
              <hr />
              <Radio.Button value={"escapeR"}>{""}</Radio.Button>
              <Radio.Button value={"leadingR"}>{""}</Radio.Button>
              <Radio.Button value={"insertR"}>{""}</Radio.Button>
              <Radio.Button value={"trackingR"}>{""}</Radio.Button>
            </Radio.Group>
          </Form.Item>
          <Form.Item label="" name={`redLevel${i}`} rules={[{ required: true }]}>
            <Rate count={3} />
          </Form.Item>
          <Form.Item label="" name={`greenLevel${i}`} initialValue={0}>
            <Rate count={3} />
          </Form.Item>
          <Form.Item label="URA" name={`uraLevel${i}`} initialValue={0}>
            <Rate count={3} />
          </Form.Item>
          <Form.Item label="" name={`whiteNum${i}`} initialValue={0}>
            <Slider min={0} max={10} dots={true} tooltipPlacement={"left"}></Slider>
          </Form.Item>
        </Col>
      );
    }
    return children;
  };
  return (
    <Form name={""} form={form} onFinish={onFinish}>
      <Alert
        message="  "
        type="info"
      ></Alert>
      <Row gutter={24}>{getFields()}</Row>
      <Row justify="end">
        <Col span={3}>
          <Form.Item label="" name={"support"} rules={[{ required: true }]}>
            <SupportInput></SupportInput>
          </Form.Item>
        </Col>
        <Col span={4} offset={1}>
          <Form.Item label="" name={"supportLevel"} initialValue={0}>
            <Rate count={4} />
          </Form.Item>
        </Col>
        <Col span={4} offset={1}>
          <Form.Item
            label="id"
            name="gameId"
            rules={[
              { required: true },
              ({ getFieldValue }) => ({
                validator(_, value) {
                  console.log("!!!!", value);
                  if (value.match(/^[0-9]\d*$/g) && value.length === 9) {
                    return Promise.resolve();
                  }
                  return Promise.reject(new Error("9id"));
                },
              }),
            ]}
          >
            <Input placeholder="id" style={{ width: "100%" }}></Input>
          </Form.Item>
        </Col>
        <Col span={2} offset={2}>
          <Form.Item>
            <Button type="primary" htmlType="submit">
              
            </Button>
          </Form.Item>
        </Col>
      </Row>
    </Form>
  );
};

const SearchOne = (props) => {
  return (
    <Form.List name={props.name}>
      {(fields, { add, remove }, { errors }) => (
        <>
          <Row>
            {fields.map((field, index) => (
              <Col key={field.key} lg={8} md={12} xs={24}>
                <Row>
                  <Form.Item
                    {...field}
                    name={[field.name, "attr"]}
                    fieldKey={[field.fieldKey, "attr"]}
                    rules={[{ required: true }]}
                    validateTrigger={["onChange", "onBlur"]}
                    noStyle
                  >
                    <Radio.Group>
                      <Radio.Button value={"speed"}>{""}</Radio.Button>
                      <Radio.Button value={"stamina"}>{""}</Radio.Button>
                      <Radio.Button value={"power"}>{""}</Radio.Button>
                      <Radio.Button value={"guts"}>{""}</Radio.Button>
                      <Radio.Button value={"wisdom"}>{""}</Radio.Button>
                      <br />
                      <Radio.Button value={"grass"}>{"/"}</Radio.Button>
                      <Radio.Button value={"dirt"}>{"/"}</Radio.Button>
                      <br />
                      <Radio.Button value={"shortDistance"}>{""}</Radio.Button>
                      <Radio.Button value={"mile"}>{""}</Radio.Button>
                      <Radio.Button value={"mediumDistance"}>{""}</Radio.Button>
                      <Radio.Button value={"longDistance"}>{""}</Radio.Button>
                      <br />
                      <Radio.Button value={"escapeR"}>{""}</Radio.Button>
                      <Radio.Button value={"leadingR"}>{""}</Radio.Button>
                      <Radio.Button value={"insertR"}>{""}</Radio.Button>
                      <Radio.Button value={"trackingR"}>{""}</Radio.Button>
                      <br />
                      <Radio.Button value={"uraLevel"}>{"URA"}</Radio.Button>
                    </Radio.Group>
                  </Form.Item>
                </Row>
                <Row>
                  <Form.Item
                    {...field}
                    name={[field.name, "level"]}
                    fieldKey={[field.fieldKey, "level"]}
                    rules={[{ required: true }]}
                  >
                    <Rate count={props.max} />
                  </Form.Item>
                </Row>
                <Col span={5}>
                  <Button type="dashed" onClick={() => remove(field.name)}>
                    
                  </Button>
                </Col>
              </Col>
            ))}
          </Row>
          <Row justify="start">
            <Form.Item>
              <Button
                type="dashed"
                onClick={() => add()}
                // style={{ width: '60%' }}
                icon={<PlusOutlined />}
              >
                
              </Button>

              <Form.ErrorList errors={errors} />
            </Form.Item>
          </Row>
        </>
      )}
    </Form.List>
  );
};

const SearchForm = (props) => {
  const [form] = Form.useForm();
  const onFinish = async (value) => {
    let formData = { attrs: [], levels: [] };
    if (value["player0"]) {
      formData.attrs.push("playerId0");
      formData.levels.push(value["player0"].id);
    }
    if (value["supportLevel"]) {
      formData.attrs.push("supportLevel");
      formData.levels.push(value["supportLevel"]);
    }
    if (value["support"]) {
      formData.attrs.push("supportId");
      formData.levels.push(value["support"].id);
    }
    value.p0?.forEach((item) => {
      if (blueLabels[item.attr]) {
        formData["blue0"] = item.attr;
        formData.attrs.push("blueLevel0");
        formData.levels.push(item.level);
      } else if (redLabels[item.attr]) {
        formData["red0"] = item.attr;
        formData.attrs.push("redLevel0");
        formData.levels.push(item.level);
      } else if (item.attr === 'uraLevel') {
        formData.attrs.push("uraLevel0");
        formData.levels.push(item.level);
      }
    });
    value.p1?.forEach((item) => {
      formData.attrs.push(item.attr);
      formData.levels.push(item.level);
    });

    props.search(formData);
  };
  const onReset = () => {
    form.resetFields();
  };
  return (
    <Form name="" form={form} onFinish={onFinish} className="seed-form">
      <Row>
        <Form.Item label="" name={`player0`}>
          <PlayerInput></PlayerInput>
        </Form.Item>
      </Row>
      <SearchOne name="p0" max="3"></SearchOne>
      <div>:</div>
      <SearchOne name="p1" max="9"></SearchOne>
      <Row>
        <Form.Item label="" name={"support"}>
          <SupportInput></SupportInput>
        </Form.Item>
        <Form.Item label="" name={"supportLevel"} initialValue={0}>
          <Rate count={4} />
        </Form.Item>
      </Row>
      <Row justify="end">
        <Form.Item>
          <Button htmlType="button" onClick={() => onReset()}>
            
          </Button>
        </Form.Item>
        <Form.Item>
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Row>
    </Form>
  );
};

const PlayerImage = (props) => {
  let imgUrl = db.get("players").find({ id: props.id }).value().imgUrl;
  return (
    <Image
      src={cdnServer + imgUrl}
      fallback="data:image/png;base64,"
      width={80}
      preview={false}
    ></Image>
  );
};

const SupportImage = (props) => {
  let support = db.get("supports").find({ id: props.id }).value();
  let imgUrl = "";
  if (support) {
    imgUrl = support.imgUrl;
  }
  return (
    <Image
      src={cdnServer + imgUrl}
      fallback="data:image/png;base64,"
      width={80}
      preview={false}
    ></Image>
  );
};

const Seed = () => {
  const [isSeedInputVisible, setIsSeedInputVisible] = useState(false);
  const [seedList, setSeedList] = useState([]);
  const [total, setTotal] = useState(0)
  const [current, setCurrent] = useState(0)
  const [value, setValue] = useState()

  const columns = [
    {
      title: "id",
      dataIndex: "gameId",
      key: "gameId",
      render: (text, seed) => (
        <>
          <Row>
            <p>{text}</p>
            <CopyToClipboard text={text} onCopy={() => message.info("")}>
              <Button shape="circle" icon={<CopyOutlined />} />
            </CopyToClipboard>
          </Row>
          <Row align="middle">
            <Button shape="circle" icon={<SmileOutlined />} onClick={() => like(seed)} />
            <p>{seed.likes}</p>
          </Row>
          <Row align="middle">
            <Button shape="circle" icon={<FrownOutlined />} onClick={() => dislike(seed)} />
            <p>{seed.dislikes}</p>
          </Row>
          {seed.userId === userId && <Row align="middle">
            <Button shape="circle" icon={<DeleteOutlined />}
              style={{ color: 'red' }} onClick={() => deleteSeed(seed)} />
          </Row>}
        </>
      ),
    },
    {
      title: "",
      dataIndex: "playerId0",
      key: "playerId0",
      render: (text) => <PlayerImage id={text}></PlayerImage>,
    },
    {
      title: "",
      dataIndex: "blue0",
      key: "blue0",
      render: (text, record) =>
        <span className="rate-label">
          {`${blueLabels[text]}\xa0\xa0${record["blueLevel0"]}`}
        </span>
      ,
    },
    {
      title: "",
      dataIndex: "red0",
      key: "red0",
      render: (text, record) =>
        <span className="rate-label">
          {`${redLabels[text]}\xa0\xa0${record["redLevel0"]}`}
        </span>
      ,
    },
    {
      title: "",
      dataIndex: "greenLevel0",
      key: "greenLevel0",
      render: (text, record) =>
        <span className="rate-label">
          {`\xa0\xa0${record["greenLevel0"]}`}
        </span>
      ,
    }, {
      title: "URA",
      dataIndex: "uraLevel0",
      key: "uraLevel0",
      render: (text, record) =>
        <span className="rate-label">
          {`${record["uraLevel0"]? `URA  ${record["uraLevel0"]}`:''}`}
        </span>
      ,
    },
    {
      title: "1",
      dataIndex: "playerId1",
      key: "playerId1",
      render: (text) => <PlayerImage id={text}></PlayerImage>,
    },
    {
      title: "2",
      dataIndex: "playerId2",
      key: "playerId2",
      render: (text) => <PlayerImage id={text}></PlayerImage>,
    },
    {
      title: "",
      key: "allBlue",
      render: (text, record) =>
        Object.keys(blueLabels).map((key) => {
          if (record[key]) {
            // console.log(key,record[key])
            return (
              <span className="rate-label">
                {`${blueLabels[key]}\xa0\xa0${record[key]}`}
              </span>
            );
          } else {
            return null;
          }
        }),
    },
    {
      title: "",
      key: "allRed",
      render: (text, record) =>
        Object.keys(redLabels).map((key) => {
          if (record[key]) {
            return (
              <span className="rate-label">
                {`${redLabels[key]}\xa0\xa0${record[key]}`}
              </span>
            );
          } else {
            return null;
          }
        }),
    },
    { title: "URA", dataIndex: "uraLevel", key: "uraLevel", render: (text) => text },
    { title: "", dataIndex: "white", key: "white", render: (text) => text },
    {
      title: "",
      dataIndex: "supportId",
      key: "supportId",
      render: (text) => <SupportImage id={text}></SupportImage>,
    },
    {
      title: "",
      dataIndex: "supportLevel",
      key: "supportLevel",
      render: (text, record) => (
        <Row>
          <Rate count={4} value={record["supportLevel"]} disabled></Rate>
        </Row>
      ),
    },
  ];

  const showSeedInput = (index) => {
    setIsSeedInputVisible(true);
  };
  const closeSeedInput = () => {
    setIsSeedInputVisible(false);
  };
  const showMySeed = () => {
    search({ attrs: ['userId'], levels: [userId] })
  }
  const deleteSeed = async (value) => {
    const res = await axios.post("https://urarawin.com/api/sqlite/delete", value);
    if (res.data) {
      message.info("");
    } else {
      message.info("");
    }
  }
  const search = async (value) => {
    setValue(value)
    const res = await axios.post("https://urarawin.com/api/sqlite/search", value);
    setCurrent(0)
    if (res.data) {
      if (res.data.count) {
        setSeedList([...res.data.list]);
        setTotal(res.data.count)
      } else {
        setSeedList([]);
        setTotal(0)
        message.info("");
      }
    } else {
      message.info("");
    }
  };
  const like = async (seed) => {
    if (!userId) {
      message.info("");
      return;
    }
    let id = seed.id;
    const res = await axios.post("https://urarawin.com/api/sqlite/like", { id, userId });
    if (res.data) {
      message.info("");
      seed.likes +=1
    }
    setSeedList([...seedList]);
  };
  const dislike = async (seed) => {
    if (!userId) {
      message.info("");
      return;
    }
    let id = seed.id;
    const res = await axios.post("https://urarawin.com/api/sqlite/dislike", { id, userId });
    if (res.data) {
      message.info("");
      seed.dislikes +=1
    }
    setSeedList([...seedList]);
  };
  const onChange = (e) => {
    search({
      ...value,
      count: e.total,
      offset: e.current * 10 - 10
    })
  }
  return (
    <>
      <div className="seed-container">
        <Card className="card" title="">
          <SearchForm search={search}></SearchForm>
          <Button onClick={() => showSeedInput()}></Button>
          <Button onClick={() => showMySeed()}></Button>
        </Card>
        <Card className="card" title="">
          <Table columns={columns} dataSource={seedList} onChange={onChange}
            pagination={{ pageSize: 10, total: total,simple:true,showQuickJumper:false,
              position: ['topRight', 'bottomRight'] }} rowKey={"id"} />
        </Card>
      </div>
      <Modal
        visible={isSeedInputVisible}
        onOk={closeSeedInput}
        onCancel={closeSeedInput}
        footer={null}
        width={"80%"}
      >
        <SeedInput onFinish={closeSeedInput}></SeedInput>
      </Modal>
    </>
  );
};
export default Seed;
